<template>
    <div id="app" ref="App">
        <transition name="showLoding">
            <div class="pageLodingShade" v-show="showPageLoding" @click="showPageLoding = false">
                <div class="smilingFace smilingFaceA">
                    <i class="leftEye EyeA"></i>
                    <i class="rightEye"></i>
                    <i class="mouth"></i>
                </div>
                <div class="welcome">Welcome</div>
                <span>点击任意地方可跳过欢迎界面哦！</span>
            </div>
        </transition>
        <transition name="showLoding">
            <div class="routerLodingShade" v-show="showRouterLoding">
                <i class="i1"></i>
                <i class="i2"></i>
                <i class="i3"></i>
                <i class="i4"></i>
                <span>Mr.Yin</span>
            </div>
        </transition>
        <div class="ground_bg"></div>

        <Header :changenavbtn="changeNavBtn"/>
        <div class="bodywrap">
            <transition name="fadeFromBottom">
                <router-view></router-view>
            </transition>
        </div>
        <Foote :showfooter="showFooter" />
    </div>
</template>

<script>
    import Header from './components/heade'
    import Foote from './components/foote'

    export default {
        name: 'App',
        data(){
            return{
                appHeight: null,
                showFooter: false,
                changeNavBtn: false,
                showPageLoding: true,
                showRouterLoding: false
            }
        },
        components: {
            Header,
            Foote
        },
        mounted (){
            let that = this
            setTimeout(function(){
                that.showPageLoding = false;
            },7000);
            window.addEventListener("scroll",this.roll);
        },
        methods: {
            roll: function(){
                if( window.scrollY > 200 ){
                    this.changeNavBtn = true;
                }else{
                    this.changeNavBtn = false;
                }
                if( window.scrollY - 0 + window.innerHeight >=  this.$refs.App.offsetHeight - 100 ){
                    this.showFooter = true;
                }else{
                    this.showFooter = false;
                }
            }
        },
        watch: {
            // 页面路由时显示loding渐变
            '$route' (to, from) {
                if( !this.showPageLoding ){
                    this.showRouterLoding = true;
                    let that = this;
                    setTimeout(function(){
                        that.showRouterLoding = false;
                    },500);
                }
            }
        }
    };
</script>

<style>
    *{ padding:0px; margin:0px }
    body, div, dl, dt, dd, ul, ol, li,h1, h2, h3, h4, h5, h6, pre, code,form,fieldset, 
    legend, input, button,textarea, p, blockquote, th, td { margin: 0; padding: 0;}
    body{ color:#000; margin: 0px auto; font-family:"Microsoft YaHei"; font-size:14px; overflow-x: hidden; }
    p{ line-height:1.8em }
    li{ list-style: none outside none; }
    a{ display: inline-block; text-decoration:none; outline:none; color: #444; }

    /*-------------------初始化结束------------------*/
    #app{ position: relative; overflow-x: hidden; }
    .pageLodingShade,.routerLodingShade{ position: fixed; top: 0; left: 0; right: 0; bottom: 0; z-index: 99999999; background: #00314f; display: flex; flex-direction: column; justify-content: center; align-items: center; }
    .pageLodingShade span{ position: absolute; right: 10px; bottom: 10px; font-size: 10px; color: #999; text-shadow: 4px 4px 5px rgba(0,0,0,.2) }
    .routerLodingShade{ z-index: 9999999; }
    .routerLodingShade i{ display: block; position: absolute; border-radius: 50%; }
    .routerLodingShade i.i1{ width: 200px; height: 200px; box-shadow: 0 0 50px 2px #a9f4fb; animation: routerLoding 10s infinite; }
    .routerLodingShade i.i2{ width: 180px; height: 200px; box-shadow: 0 0 50px 2px #9c06b6; animation: routerLoding 4.8s reverse infinite; }
    .routerLodingShade i.i3{ width: 190px; height: 170px; box-shadow: 0 0 50px 2px #b7176c; animation: routerLoding 8s infinite; }
    .routerLodingShade i.i4{ width: 200px; height: 190px; box-shadow: 0 0 50px 2px #ff6f64; animation: routerLoding 5.2s reverse infinite; }
    .routerLodingShade span{ font-size: 20px; color: #FFF; font-weight: bold; }
    .smilingFace{ display: block; position: relative; width: 200px; height: 200px; border-radius: 50%; background: #20293a; box-shadow: 0 0 100px 10px #224a64;
        text-align: center; line-height: 200px; font-size: 30px; font-weight: bold; color: #FFF; font-style: normal; }
    .welcome{ font-size: 24px; color: #224a64; opacity: 0; animation: fadeIn 1s 5s both; }
    .leftEye,.rightEye{ position: absolute; top: 30%; width: 20px; height: 25px; border: 2px solid #FFF; border-radius: 50%; box-shadow: 0 0 10px 2px #224a64; opacity: 0; }
    .leftEye{ left: 25%; animation: lEye 4s .5s both; }
    .rightEye{ right: 25%; animation: rEye .5s 2s both; }
    .mouth{ position: absolute; bottom: 25%; left: 0; right: 0; margin: auto; width: 90px; height: 30px; border-radius: 50%; border-bottom: 2px solid #FFF; opacity: 0; animation: Mouth .5s 3s both; }
    .ground_bg{ background: url(http://p1.pstatp.com/origin/pgc-image/a9ac9adefa004fd2b223997e423bbf93) center no-repeat;
        -moz-background-size: cover; -o-background-size: cover; -webkit-background-size: cover; background-size: cover;
        top: 0; left: 0; position: fixed; width: 100%; height: 100%; z-index: -999; }
    .bodywrap{ position: relative; background: #00314f; z-index: 1; opacity: 0.98; }
    .bodywrap:before,.bodywrap:after{ content: ""; display: block; width: 200%; height: 400px; border-radius: 50%; background: #00314f;
        position: absolute; left: 0; right: 0; margin-left: -50%; z-index: -999;
    }
    .bodywrap:before{ top: -40px; }
    .bodywrap:after{ bottom: -40px; }
    .infoarticlewrap{ position: relative; background: #FFF; z-index: 2; opacity: 0.98; padding: 15px; }
    .infoarticlewrap:before,.infoarticlewrap:after{ content: ""; display: block; width: 200%; height: 400px; border-radius: 50%; background: #FFF;
        position: absolute; left: 0; right: 0; margin-left: -50%; z-index: -999;
    }
    .infoarticlewrap:before{ top: -40px; }
    .infoarticlewrap:after{ bottom: -40px; }
    .infoarticletitle{ font-size: 20px; color: #444; text-align: center; }
    .statisticswrap{ padding: 10px 0; font-size: 12px; color: #999; display: flex; justify-content: center; }
    .statisticswrap>span{ padding: 0 4px; }
    .infoarticlecontent{ font-size: 15px; color: #666; line-height: 2.0em; max-width: 800px; margin: 0 auto; }
    .infoarticlecontent img{ display: block; max-width: 100%; margin: 0 auto; padding: 20px 0; }
    .router-link-exact-active{ color: #ccc !important; }

    @media (max-width: 800px){
        .router-link-exact-active{ background: #444; }
        .bodywrap:before{ height: 200px; top: -20px; }
        .bodywrap:after{ height: 200px; bottom: -20px; }
    }

    .fadeFromBottom-enter-active{
        animation: fadeFromBottom 1s both;
    }
    .showLoding-leave-active{
        animation: fadeOut 1s;
    }
    /*-------------------公用CSS3动画------------------*/
    @charset "UTF-8";

    /*-loding-s--*/
    @keyframes smilingFaceA {
        from {
            box-shadow: 0 0 200px 20px #224a64;
        }
        90%{
            transform: translateY(-40px);
        }
        to {
            transform: translateY(-40px);
            box-shadow: 0 0 200px 20px #20293a;
        }
    }
    .smilingFaceA{
        animation: smilingFaceA 2s 5s both;
    }
    @keyframes lEye {
        from {
            transform: scale(0);
            opacity: 0;
        }
        20%{
            transform: scale(1);
            opacity: 1;
        }
        40%{
            transform: scale(1.1);
            opacity: 1;
        }
        60%{
            transform: scale(1.2);
            opacity: 1;
        }
        80%{
            transform: scale(0);
            opacity: 0;
        }
        90%{
            transform: scale(1);
            opacity: 1;
        }
        to {
            transform: scale(1);
            opacity: 1;
        }
    }
    @keyframes rEye {
        from {
            transform: scale(0);
            opacity: 0;
        }
        90%{
            transform: scale(1);
            opacity: 1;
        }
        to {
            transform: scale(1);
            opacity: 1;
        }
    }
    @keyframes Mouth {
        from {
            transform: scale(0);
            opacity: 0;
            border-bottom: 2px solid #FFF;
        }
        50%{
            transform: scale(1);
            opacity: 1;
            border-bottom: 2px solid #FFF;
        }
        to {
            transform: scale(1);
            opacity: 1;
            border-bottom: 8px solid #FFF;
        }
    }
    @keyframes routerLoding {
        from {
            transform: rotate(0deg);
        }
        50%{
            transform: rotate(360deg);
        }
        to {
            transform: rotate(0deg);
        }
    }
    /*-loding-f--*/

    /*-渐显-s--*/
    @keyframes fadeIn {
        from {
            opacity: 0;
        }
        to {
            opacity: 1;
        }
    }
    .fadeIn{
        animation: fadeIn 1s both;
    }
    /*-渐显-f--*/

    /*-渐隐-s--*/
    @keyframes fadeOut {
        from {
            opacity: 1;
        }
        to {
            opacity: 0;
        }
    }
    .fadeOut{
        animation: fadeOut 1s both;
    }
    /*-渐隐-f--*/

    /*-从右渐显-s--*/
    @keyframes slideFadeFromRight {
        from {
            opacity: 0;
            transform: translateX(100%);
        }
        to {
            opacity: 1;
            transform: translateX(0);
        }
    }
    .slideFadeFromRight{
        animation: slideFadeFromRight 1s both;
    }
    /*-从右渐显-f--*/

    /*-向右渐隐-s--*/
    @keyframes slideFadeToRight {
        from {
            opacity: 1;
            transform: translateX(0);
        }
        to {
            opacity: 0;
            transform: translateX(100%);
        }
    }
    .slideFadeToRight{
        animation: slideFadeToRight 1s both;
    }
    /*-向右渐隐-f--*/

    /*-底部渐出-s--*/
    @keyframes fadeFromBottom {
        from {
            opacity: 0;
            transform: translateY(50px);
        }
        to {
            opacity: 1;
            transform: translateY(0);
        }
    }
    .fadeFromBottom{
        animation: fadeFromBottom 1s both;
    }
    /*-底部渐出-f--*/

    /*--s-缩放 开始-*/
    @keyframes scaleOut {
        from {
            opacity: 1;
            transform: scale(1);
        }
        to {
            opacity: 0;
            transform: scale(0);
        }
    }
    .scaleOut{
        animation: scaleOut 1s both;
    }
    @keyframes scaleIn {
        from {
            opacity: 0;
            transform: scale(0);
        }
        to {
            opacity: 1;
            transform: scale(1);
        }
    }
    .scaleIn{
        animation: scaleIn 1s both;
    }
    /*--f-缩放 结束-*/

    /*右上角导航按钮 开始*/
    /*--s--*/
    @keyframes clockwiseRotate {
        from {
            transform: translateY(0) rotate(0deg);
        }
        to {
            transform: translateY(11.5px) rotate(45deg);
        }
    }
    @keyframes anticlockwiseRotate {
        from {
            transform: translateY(0) rotate(0deg);
        }
        to {
            transform: translateY(-11.5px) rotate(-45deg);
        }
    }
    /*r*/
    @keyframes clockwiseRotatereverse {
        from {
            transform: translateY(11.5px) rotate(45deg);
        }
        to {
            transform: translateY(0) rotate(0deg);
        }
    }
    @keyframes anticlockwiseRotatereverse {
        from {
            transform: translateY(-11.5px) rotate(-45deg);
        }
        to {
            transform: translateY(0) rotate(0deg);
        }
    }
    .clockwiseRotate{
        animation: clockwiseRotate 1s both;
    }
    .clockwiseRotatereverse{
        animation: clockwiseRotatereverse 1s both;
    }
    .anticlockwiseRotate{
        animation: anticlockwiseRotate 1s both;
    }
    .anticlockwiseRotatereverse{
        animation: anticlockwiseRotatereverse 1s both;
    }
    /*--f--*/
    /*--s--*/
    @keyframes slideRight {
        from {
            transform: translateX(0) rotate(0) scale(1);
        }
        50%{
            transform: translateX(12.5px) rotate(45deg) scale(1);
        }
        to {
            transform: translateX(12.5px) rotate(45deg) scale(0.5,6);
        }
    }
    .slideRight{
        animation: slideRight 1s both;
    }
    /*--f--*/
    /*--s--*/
    @keyframes slideLeft {
        from {
            transform: translateX(0) rotate(0) scale(1);
        }
        50%{
            transform: translateX(-12.5px) rotate(-45deg) scale(1);
        }
        to {
            transform: translateX(-12.5px) rotate(-45deg) scale(0.5,6);
        }
    }
    .slideLeft{
        -webkit-animation: slideLeft 1s both;
        animation: slideLeft 1s both;
    }
    /*--f--*/
    /*--s--*/
    @keyframes slideRightOut {
        from {
            transform: translateX(12.5px) rotate(45deg) scale(0.5,6);
        }
        50%{
            transform: translateX(12.5px) rotate(45deg) scale(1);
        }
        to {
            transform: translateX(0) rotate(0) scale(1);
        }
    }
    .slideRightOut{
        animation: slideRightOut 1s both;
    }
    /*--f--*/
    /*--s--*/
    @keyframes slideLeftOut {
        from {
            transform: translateX(-12.5px) rotate(-45deg) scale(0.5,6);
        }
        50%{
            transform: translateX(-12.5px) rotate(-45deg) scale(1);
        }
        to {
            transform: translateX(0) rotate(0) scale(1);
        }
    }
    .slideLeftOut{
        animation: slideLeftOut 1s both;
    }
    /*--f--*/
    /*右上角导航按钮 结束*/
</style>



